import React from 'react';

function Card({id, name, email}) {
    return (
        <div className='tc bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5  }'>
            <img src={`https://robohash.org/${id}?size=200x200`} alt={`${name}-Robo`}/>
            <div>
                <h2>{name}</h2>
                <h3>{email}</h3>
            </div>
        </div>
    );
}

export default Card;